@import url('https://fonts.googleapis.com/css2?family=Oswald:wght@500&display=swap');


/* 기본 설정 */
html, body, .App, #root {
    width: 100%;
    height: 100%;
    margin: 0;
    padding: 0;
    -webkit-user-select: none;
    -moz-user-select: none;
    -ms-use-select: none;
    user-select: none;
    /* overflow-y: hidden; */
}

body {
    background-color: white;
}


nav {
    width: 100%;
    height: 80px;
    background-color: rgb(107, 107, 255);
    color: white;
    display: flex;
    justify-content: space-around;
    align-items: center;
    font-family: 'Oswald', sans-serif;
    position: fixed;
    z-index: 1;
}

a {
    text-decoration: none;
    color: white;
}

.navbar-left {
    margin: 0 70px;
    cursor: pointer;
    font-size: x-large;
}

.navbar-right {
    display: flex;
}

.navbar-right-btn {
    margin: 0 20px;
    cursor: pointer;
}

.navbar-right-btn:hover {
    color:rgb(255, 177, 68);
    transition: all 0.5s;
}
