#home {
    width: 100%;
    height: 100%;
    /* background-color: rgb(10, 0, 70); */
    background: linear-gradient(-50deg, rgb(255, 204, 204) 50%, rgb(10, 0, 70) 50%);
}

.home-title-box {
    height: 100%;
    margin: 0 50px;
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
}

.home-title {
    font-family: 'Oswald', sans-serif;
    color: white;
    opacity: 0.4;
    width: 70%;
    /* padding-top: 100px; */
    font-size: 100px;
    word-wrap: break-word;
    margin-top: 30px;
    
}

.home-content {
    color: white;
    width: 21.5ch;
    height: 40px;
    /* margin: 1px 0px; */
    animation: typing 5s steps(15), blink .4s step-end infinite alternate;
    overflow: hidden;
    white-space: nowrap;
    border-right: 3px solid;
    font-family: 'Oswald', sans-serif;
    font-size: x-large;
}

.home-middle {
    width: 100%;
    color: white;
}



.profile-img {
    width: 200px;
    height: 200px;
    border-radius: 50%;
    /* position: absolute;
    top: 12%;
    right: 10%; */
    animation: 4s linear rotate 0s infinite ;
}

@keyframes typing {
    from {
        width: 0
    }
}

@keyframes blink {
    50% {
        border-color: transparent
    }
}

@keyframes rotate {
    from {
        transform:rotate(0deg);
    }
    to {
        transform: rotate(360deg);
    }
}